import React, { useEffect, useState } from "react";
import Axios from "axios";
import { Link } from "react-router-dom";
function Teams() {
  const [teamsList, setTeamsList] = useState([]);
  useEffect(() => {
    const getTeamsList = async () => {
      const { data: post } = await Axios.get(
        "http://192.168.68.143:3002/getTeams"
      );
      setTeamsList(post);
    };
    getTeamsList();
  });
  return (
    <>
      <div className="container mt-3">
        Click to See Stats:
        <table className="table">
          <thead>
            <tr>
              <th className="col">Team Number</th>
              <th className="col">Team Name</th>
            </tr>
          </thead>
          <tbody>
            {teamsList.map((value, key) => {
              return (
                <>
                  <tr>
                    <Link to={`/team/${value.team_number}`}>
                      <td>{value.team_number}</td>
                    </Link>
                    <td style={{ marignRight: "500px" }}>{value.team_name}</td>
                  </tr>
                </>
              );
            })}
          </tbody>
        </table>
      </div>
    </>
  );
}

export default Teams;
